// Global block style
#main-wrapper {
  .block {
    overflow: hidden;

    &:not(.block-no-padding) {
      padding-top: $block-padding/3;
      padding-bottom: $block-padding/3;
    }
  }

  .row-block-padding {
    margin-top: -$block-padding/6;
    margin-bottom: -$block-padding/6;

    > .col,
    > [class*="col-"] {
      padding-top: $block-padding/6;
      padding-bottom: $block-padding/6;
    }
  }

  @include media-breakpoint-up(md) {
    .block {
      &:not(.block-no-padding) {
        padding-top: $block-padding;
        padding-bottom: $block-padding;
      }

      .btn-long {
        padding-right: 5rem;
        padding-left: 5rem;
      }
    }

    .row-block-padding {
      margin-top: -$block-padding/2;
      margin-bottom: -$block-padding/2;

      > .col,
      > [class*="col-"] {
        padding-top: $block-padding/2;
        padding-bottom: $block-padding/2;
      }
    }
  }
}

// Block Team Slide.
@include media-breakpoint-up(sm) {
  .block--team-slides {
    .team-slide--item {
      &:nth-child(even) {
        .row-even {
          flex-direction: row-reverse;
        }

        .image-wrapper {
          justify-content: flex-end;
        }

        @include media-breakpoint-up(xl) {
          .col-xl-4 {
            margin-right: 8.333333%;
          }
        }
      }
    }
  }
  h2 {
    font-size: 4rem;
  }
  h3 {
    font-size: 3rem;
  }
}

.subtitle {
  font-size: 1.7rem;
}

.team-slide--item-content {
  font-size: 1.8rem;
}

// Item team slide.
.team-slide--item {
  .content-wrapper  {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @include media-breakpoint-up(md) {
    .content-wrapper {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    h2 {
      font-size: 6rem;
      margin-bottom: 5rem;
    }
    h3 {
      font-size: 4.5rem;
    }
  }
}

// Special class for Block company numbers just for Page Our team.
.block--company-numbers {
  order: 2;
}

// Our team members.
.member-wrapper {
  .image-wrapper {
    max-width: 100%;
    width: 146px;
  }

  .member-name {
    font-family: $fontFamily-semibold;
  }

  @include media-breakpoint-up(md) {
    .image-wrapper {
      max-width: 100%;
      width: 246px;
    }

    .member-name {
      font-size: 3rem;
    }

    .memeber-title {
      font-size: 2rem;
    }
  }

  .grayscale {
    filter: grayscale(1);
  }
}

@include media-breakpoint-only(xs) {
  h2 {
    font-size: 4rem;
  }
  h3 {
    font-size: 2.5rem;
  }
}

// Joining our team.
.block--joining-our-team {
  padding: 0 !important;
  background-color: #989898;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.our-team-image {
  max-height: 700px;
}
